*{
    margin: 0px;
    padding: 0px;
}
.header{
    display: flex; 
    align-items: center; 
    justify-content:space-evenly;

    background-color: white;  

    
}
 .header__logo{

   object-fit: contain;
   background-color: white;
   padding: 10px;
   height: 40px;
   margin-left: -40px;
   object-fit: contain;
   flex: none;
   cursor: pointer; 

} 
.header__left{
    display: flex;
    

}
.arrow{
   margin-top: 18px;
   margin-left: -12px;
   cursor: pointer;

}

.header__mid{
    align-items: center;
    display: flex;
    flex-direction: row;
    background-color: white;
      
}
.input{
    width: 180px;
}
 .header__inputbutton{
    width: 40px !important;
    background-color: #fafafa; 
   
} 
.header__right{
    display: flex;
    align-items: center;
  
    
}
.icon{
    margin-right: 10px;
    cursor: pointer;
} 
.main {
    width: 50%;
    margin: 50px auto;
}



.has-search .form-control {
    padding-left: 2.375rem;
}

.has-search .form-control-feedback {
    position: absolute;
    z-index: 2;
    display: block;
    width: 2.375rem;
    height: 2.375rem;
    line-height: 2.375rem;
    text-align: center;
    pointer-events: none;
    color: #aaa;
}